import { Component, OnInit, ViewChild } from '@angular/core';
import { MatPaginator } from '@angular/material/paginator';
import { MediaChange, MediaObserver } from '@angular/flex-layout';
import { PerfectScrollbarConfigInterface } from 'ngx-perfect-scrollbar';
import { Subscription } from 'rxjs';
import { debounceTime, distinctUntilChanged } from 'rxjs/operators';
import { Settings, AppSettings } from '../../app.settings';
import { AppService } from '../../app.service';
import { Property, Pagination } from '../../app.models';
import { PropertyInput} from './models/property.input';
import { GlobalState } from 'src/app/store/states/global.state';
import { Store, select } from '@ngrx/store';
import { PropertyInsertAction, PropertyLoadAction } from './store/property.actions';
import { PropertyLoad } from './models/property.load';
import {selectAllProperty, selectPropertyTotal} from './store/property.selectors';


@Component({
  selector: 'app-properties',
  templateUrl: './properties.component.html',
  styleUrls: ['./properties.component.scss']
})
export class PropertiesComponent implements OnInit {
  @ViewChild('sidenav') sidenav: any;
  public sidenavOpen = true;
  @ViewChild(MatPaginator) paginator: MatPaginator;
  public psConfig: PerfectScrollbarConfigInterface = {
    wheelPropagation: true
  };
  public properties: Property[] = [];
  public viewType = 'grid';
  public viewCol = 33.3;
  public count = 10;
  public sort: string;
  public limit: number;
  public page = 1;
  public total = 0;
  public searchFields: any;
  public removedSearchField: string;
  public pagination: Pagination = new Pagination(1, this.count, null, 2, 0, 0);
  public message: string;
  public watcher: Subscription;

  public settings: Settings;
  constructor(public appSettings: AppSettings, public appService: AppService, public mediaObserver: MediaObserver, private store: Store<GlobalState>) {
    this.settings = this.appSettings.settings;
    this.watcher = mediaObserver.media$.subscribe((change: MediaChange) => {
      if (change.mqAlias == 'xs') {
        this.sidenavOpen = false;
        this.viewCol = 100;
      }
      else if (change.mqAlias == 'sm'){
        this.sidenavOpen = false;
        this.viewCol = 50;
      }
      else if (change.mqAlias == 'md'){
        this.viewCol = 50;
        this.sidenavOpen = true;
      }
      else{
        this.viewCol = 33.3;
        this.sidenavOpen = true;
      }
    });

  }

  ngOnInit() {

   // this.getProperties();
   this.store.pipe(select(selectAllProperty)).subscribe(data => {
       this.properties = data;
  } );

   this.store.pipe(select(selectPropertyTotal)).subscribe(total => {
     this.total = total;
     const data = new Array(total);
     const result = this.filterData(data);
     if (result.data.length == 0){
      this.properties.length = 0;
      this.pagination = new Pagination(1, this.count, null, 2, 0, 0);
      this.message = 'No Results Found';
      return false;
    }

     this.pagination = result.pagination;
     this.message = null;

   });

   this.loadProperties({page: 1, limit: 10});
  }

  ngOnDestroy(){
    this.watcher.unsubscribe();

  }

  public getProperties(){


    // this.appService.getProperties().subscribe(data => {
    //   const result = this.filterData(data);
    //   if (result.data.length == 0){
    //     this.properties.length = 0;
    //     this.pagination = new Pagination(1, this.count, null, 2, 0, 0);
    //     this.message = 'No Results Found';
    //     return false;
    //   }
    //   this.properties = result.data;
    //   this.pagination = result.pagination;
    //   this.message = null;
    // });
  }

  public resetPagination(){
    if (this.paginator){
      this.paginator.pageIndex = 0;
    }
    this.pagination = new Pagination(1, this.count, null, null, this.pagination.total, this.pagination.totalPages);
  }

  public filterData(data){
   return this.appService.filterData(data, this.searchFields, this.sort, this.pagination.page, this.pagination.perPage);
  }

  public loadProperties(data){
    this.store.dispatch(new PropertyLoadAction(data));
  }

  public searchClicked(){
    this.properties.length = 0;
    this.getProperties();
    window.scrollTo(0, 0);
  }
  public searchChanged(event){
    event.valueChanges.subscribe(() => {
      this.resetPagination();
      this.searchFields = event.value;
      setTimeout(() => {
        this.removedSearchField = null;
      });
      if (!this.settings.searchOnBtnClick){
        this.properties.length = 0;
      }
    });
    event.valueChanges.pipe(debounceTime(500), distinctUntilChanged()).subscribe(() => {
      if (!this.settings.searchOnBtnClick){
        this.getProperties();
      }
    });
  }
  public removeSearchField(field){
    this.message = null;
    this.removedSearchField = field;
  }


  public changeCount(count){
    this.count = count;
    this.properties.length = 0;
    this.resetPagination();
    this.getProperties();
  }
  public changeSorting(sort){
    this.sort = sort;
    this.properties.length = 0;
    this.getProperties();
  }
  public changeViewType(obj){
    this.viewType = obj.viewType;
    this.viewCol = obj.viewCol;
  }


  public onPageChange(e){
    const nextPage =  e.pageIndex + 1;
    this.pagination.page = nextPage;
    this.loadProperties({page: nextPage, limit: 10});
    window.scrollTo(0, 0);
  }

}
